.section__title {
    text-align: center;
    margin-bottom: 4rem;
}

.section__title h4 {
    font-size: 2rem;
    letter-spacing: var(--mainSpacing);
    text-transform: uppercase;
    margin-bottom: 1rem;
}

.section__title div {
    width: 5rem;
    height: 5px;
    margin: 0 auto;
    background: var(--primaryColor);
}