.room {
    box-shadow: var(--lightShadow);
    transition: var(--mainTransition);
}

.room:hover {
    box-shadow: var(--darkShadow);
}

.img-container {
    position: relative;
}

.img-container img {
    width: 100%;
    height: 250px;
    display: block;
    transition: var(--mainTransition);
    object-fit: cover;
}

.price-top {
    position: absolute;
    top: 0;
    left: 0;
    background: var(--primaryColor);
    color: var(--mainWhite);
    padding: 0.3rem 0.6rem 0.5rem;
    border-bottom-right-radius: 1rem;
    font-size: 0.5rem;
    text-align: center;
    transition: var(--mainTransition);
}

.price-top h6 {
    margin-bottom: 0;
    font-size: 0.9rem;
    font-weight: 300;
    letter-spacing: var(--mainSpacing);
}

.room-link {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: scale(0);
    transition: all 0.3s linear;
}

.img-container:hover {
    background: rgba(0, 0, 0, 0.8);
}

.img-container:hover img {
    opacity: 0.3;
}

.img-container:hover .price-top {
    opacity: 0;
}

.img-container:hover .room-link {
    transform: translate(-50%, -50%) scale(1);
}

.room-info {
    background: var(--mainBlue);
    color: var(--mainWhite);
    text-transform: capitalize;
    padding: 0.7rem 0;
    text-align: center;
    font-weight: 700;
    letter-spacing: var(--mainSpacing);
}